  color: #ffccbc;
}

.navbar .nav {
  display: flex;
  transition: all 0.4s ease;
}

.container {
  height: 50%;
}

.row {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5em;
}

.form-container {
  align-self: center;
  padding: 0.5em;
  width: 40%;
  height: 60%;
  margin: 8em auto;
  border: 2px solid #b0bec5;
  border-radius: 6px;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.3);
  transition: all 2s ease;
}

.form-container .danger {
  background: #e57373;
}

.form-container h3 {
  text-align: center;
  text-shadow: 0.5px 0.5px 1px white;
}

.form-container form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  width: 40%;
}

.form-container form input {
  width: 60%;
  padding: 0.6em 1em;
  margin: 0.5em auto;
  font-size: 1rem;
  border-radius: 3px;
  border: 2px solid #e0e0e0;
  outline: none;
  transition: all 0.4s ease;
  box-shadow: 1px #e0e0e0 inset;
}

.form-container form input:focus,
.form-container form input:active {
  border: 2px solid #82ada9;
}

.form-container form input:valid {
  border: 2px solid #66bb6a;
}

.form-container form button {
  width: 50%;
  margin: 0.5em auto;
  background-color: brown;
  border: none;
}

.form-container form button:hover {
  box-shadow: 3px 8px 16px rgba(0, 0, 0, 0.5);
}

.form-container form a {
  margin: auto;
}

.form-container form label {
  margin-top: 0.6em;
  text-shadow: 0.5px 0.25px 0.1px white;
  font-family: "Montserrat";
  font-weight: 400;
  text-align: left;
}

